body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-quicksand: Quicksand;
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-13xl: 32px;
  --font-size-6xl: 25px;
  --font-size-12xl: 31px;
  --font-size-11xl: 30px;

  /* Colors */
  --color-black: #000;
  --color-white: #fff;
  --color-gainsboro-100: #e6e7e9;
  --color-gainsboro-200: #d9d9d9;
  --color-gray-100: #010001;
  --color-lavender: #f9ebff;
  --color-silver: #b5bec4;
  --mainbg: #fffcfc;

  /* border radiuses */
  --br-xs: 12px;
  --br-8xs: 5px;
}
